import { useState, useEffect } from 'react'
import styles from '../../css/ProgramsComponents/programComponents.module.css'

//MUI
import { Grid } from '@mui/material'

//Custom components
import CheckboxDropdown from './CheckboxDropdown'

// Section where the two dropdown menus are kept

export default function FilterArea(props) {
  // Keep track of window size
  const [windowSize, setWindowSize] = useState([
    window.innerWidth,
    window.innerHeight,
  ])

  useEffect(() => {
    const handleWindowResize = () => {
      setWindowSize([window.innerWidth, window.innerHeight])
    }
    window.addEventListener('resize', handleWindowResize)
    return () => {
      window.removeEventListener('resize', handleWindowResize)
    }
  })

  const handleTypeCheck = (typesChecked) => {
    props.setCheckedItems({
      ...props.checkedItems, // Copy the old fields
      types: typesChecked, // But override this one
    })
  }

  const handleProfessionChecked = (professionsChecked) => {
    props.setCheckedItems({
      ...props.checkedItems, // Copy the old fields
      professions: professionsChecked, // But override this one
    })
  }

  // Adjust layout based on screen width
  var dropdownWidth = 5.5
  if (windowSize[0] < 767) {
    dropdownWidth = 12
  }

  return (
    <Grid container className={styles.boxContainer}>
      <Grid item  xs={dropdownWidth}>
        <CheckboxDropdown 
          onItemChecked={handleTypeCheck}
          label='Typ av program'
          items={['Civilingenjr', 'Kandidat', 'Hgskoleingenjr', 'Master']}
        />
      </Grid>
      <Grid item  xs={dropdownWidth}>
        <CheckboxDropdown
          onItemChecked={handleProfessionChecked}
          label='Kompetenser'
          items={[
            "Additiv Tillverkning",
            "Analytisk Kemi",
            "Anatomi",
            "Astronomi",
            "Automatisering",
            "Batteriteknik",
            "Berkningskemi",
            "Berkningsvetenskap",
            "Bildanalys",
            "Biofysik",
            "Bioinformatik",
            "Biokemi",
            "Biologi",
            "Bioteknik",
            "Byggkonstruktion",
            "Byggproduktion",
            "Byggteknik",
            "Cad",
            "Dataanalys",
            "Datavetenskap",
            "Ekologi",
            "Ekonomi",
            "Elektrifiering",
            "Elektromobilitet",
            "Elektronik",
            "Elektroteknik",
            "Elkraftteknik",
            "Energifysik",
            "Energilagring",
            "Energiteknik",
            "Evolutionsbiologi",
            "Finansiell Matematik",
            "Framfra Provresultat",
            "Fundamental Och Tillmpad Fysik",
            "Fysik",
            "Fysikalisk Kemi",
            "Fysiologi",
            "Frvaltning",
            "Geofysik",
            "Geologvetenskap",
            "Geovetenskap",
            "Gis",
            "Humaniora",
            "Hllbar Utveckling",
            "Inbyggda System",
            "Industriell Ekonomi",
            "Industriell Teknik",
            "Ingenjrsmssighet",
            "Kemi",
            "Kretsanalys",
            "Kvantfysik",
            "Kvantteknologi",
            "Krnfysik",
            "Laborativt Arbete",
            "Ledarskap",
            "Logik",
            "Lkemedels - Utvekling - Tillverkning -Analys",
            "Maskininlrning",
            "Matematik",
            "Materialanalys",
            "Materialkemi",
            "Materialteknik",
            "Materialvetenskap",
            "Medicinsk Fysik",
            "Medicinsk Teknik",
            "Medicinteknik",
            "Metrologi",
            "Mikro Och Nanoteknik",
            "Miljteknik",
            "Modellering",
            "Molekylr Bioteknik",
            "Molekylrbiologi",
            "Naturvetenskapligt Arbetsstt",
            "Oorganisk Kemi",
            "Organisk Kemi",
            "Organiskkemi",
            "Paleobiologi",
            "Partikelfysik",
            "Problemlsningsfrmga",
            "Process Teknik",
            "Produktionsledning",
            "Produktutveckling",
            "Programmering",
            "Projektledning",
            "Provberedning",
            "Samhllsplanering",
            "Samhllsvetenskap",
            "Sedimentologi",
            "Statistik",
            "Styrsystem",
            "Systemutveckling",
            "Systemvetenskap",
            "Teknisk Kemi",
            "Tillverkning",
            "Tillmpad Bioteknik",
            "Tillmpad Fysik",
            "Tillmpad Krnkraftteknik",
            "Vattenresurser"
            
          ]}
        />
      </Grid>
    </Grid>
  )
}
